




















import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'

@Component({ components: {} })
export default class PopupDelete extends Mixins(PopupMixin, DistrictMixin) {
  @Prop()
  data?: Record<string, any>
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popupDeleteContract',
      isOpen: false
    })
  }
  handleConfirm(): void {
    this.$emit('delete')
    this.setOpenPopup({
      popupName: 'popupDeleteContract',
      isOpen: false
    })
  }
  handleOpen(): void {
    console.log('open')
  }
  handleClose(): void {
    console.log('close')
  }
}
