

























































































import { Component, Mixins } from 'vue-property-decorator'
import ContractRepository from '@/services/repositories/contract'
import { PaginationInterface } from '@/interface'
import { namespace } from 'vuex-class'
import getRepository from '@/services'
import { forEach } from 'lodash'
import PopupMixin from '@/mixins/popup'
import SearchBar from '../../../audit/components/filters/Searchbar.vue'
import ExportMixin from '../../../contracts/mixins/export'
import FilterMixin from '@/mixins/filterAuditContractMixin'
import AuditPopupFilter from '@/modules/audit/components/popup/AuditPopupFilter.vue'
import CreateCost from '../../../contracts/components/popup/CreateCost.vue'
import DetailContract from '@/modules/contracts/components/popup/DetailContract.vue'
import ActionContract from '@/modules/contracts/components/popup/ActionContract.vue'
import DeleteContract from '@/modules/contracts/components/popup/DeleteContract.vue'

const beAudit = namespace('beAudit')
const beCustomer = namespace('beCustomer')
@Component({ components: { PopupMixin, SearchBar, AuditPopupFilter, CreateCost,DetailContract, ActionContract,DeleteContract } })
export default class Payment extends Mixins(PopupMixin, ExportMixin, FilterMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }
  api!: ContractRepository
  @beCustomer.State('detailCustomer') detailCustomer!: Record<string, any>
  data = []
  isLoading = true
  isConflickClick = false
  query: PaginationInterface = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    contractStatus: '',
    searchType: 'CONTRACT',
    customerId: ''
  }
  dataEdit: any = {
    title: '',
    code: '',
    type: '',
    bankContractNo: ''
  }
  contractInfor: Record<string, any> = {}

  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })
    this.query.orderByClause = command.id
    this.init()
  }
  option: Array<Record<string, any>> = [
    {
      id: 1,
      name: 'Đơn vị kinh doanh',
      divided: false
    },
    {
      id: 6,
      name: 'Tổng phí',
      divided: true
    },
    {
      id: 2,
      name: 'Ngày phát hành',
      divided: true
    },
    {
      id: 3,
      name: 'Ngày đóng phí',
      divided: false
    },
    {
      id: 4,
      name: 'Ngày hết hạn',
      divided: false
    },
    {
      id: 5,
      name: 'Mã sản phẩm',
      divided: true
    }
  ]
  checkTypeContract(type: string): void {
    switch (type) {
      case 'new':
        this.query.contractStatus = 1
        break
      case 'cancel':
        this.query.contractStatus = 2
        break
      case 'renew':
        this.query.contractStatus = 3
        break

      default:
        this.query.contractStatus = null
        break
    }
  }
  responses: any = []
  init(): void {
    this.isLoading = true
    this.checkTypeContract(this.$route?.name as string)
    const temp = {
      ...this.query,
      total: null,
      orderByClause: this.query.orderByClause ? this.query.orderByClause : null
      // contractStatus: this.$route.name === 'contract' ? '' : this.$route.name?.toUpperCase()
    }
    this.api
      .getData(temp)
      .then((res: any) => {
        this.data = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.api.getContractCount(temp).then(res => {
      this.query.total = res.data
    })
  }
  handleFilter(value: Record<string, any>): void {
    let check = this.$route.name
    if (check) {
      this.query = {
        ...this.query,
        ...value,
        contractStatus: this.checkDataRoute(check)
      }
      this.query.page = 1
      this.api
        .getData(this.query)
        .then((res: any) => {
          this.data = res.data
          this.isLoading = false
        })
        .catch(() => {
          this.isLoading = false
        })
      this.api.getContractCount(this.query).then(res => {
        this.query.total = res.data
      })
    }
  }
  checkDataRoute(value: string): any {
    if (value === 'new') {
      return 1
    }
    if (value === 'cancel') {
      return 2
    }
    if (value === 'renew') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'contract') {
      return ''
    }
    if (value === 'pending') {
      return 3
    }
    if (value === 'refuse') {
      return 4
    }
  }
  handleSearch(key: string): void {
    this.query.search = key
    this.resetQuery()
    this.init()
  }
  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }

  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }

  checkStatus(item: Record<string, any>): string {
    let statusText = ''
    let classStatus = ''
    if (item.contractStatus === '1') {
      switch (item.expireStatus) {
        case '1':
          statusText = 'Mới'
          classStatus = 'effective'
          break
        case '2':
          statusText = 'Hủy'
          classStatus = 'cancel'
          break
        default:
          statusText = 'Mới'
          classStatus = 'effective'
          break
      }
    } else if (item.contractStatus === '2') {
      statusText = 'Hủy'
      classStatus = 'cancel'
    } else if (item.contractStatus === '3') {
      statusText = 'Tái tục'
      classStatus = 'effective'
    } else if (item.contractStatus === '4') {
      statusText = 'Mất hiệu lực'
      classStatus = 'lapse'
    }

    return '<span class="' + classStatus + ' statuss">' + statusText + '</span>'
  }
  handleClickFillter(): void {
    this.setOpenPopup({
      popupName: 'audit-filter',
      isOpen: true
    })
  }

  handleCreateCost(): void {
    this.dataEdit.type = 'khach-hang'
    this.dataEdit.title = 'Thêm mới thu phí'
    this.dataEdit.code = ''
    this.setOpenPopup({
      popupName: 'create-cost',
      isOpen: true
    })
  }
  created(): void {
    this.api = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.query.customerId = this.detailCustomer.id
    this.init()
  }
  handleRowClick(row: Record<string, any>): void {
    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }
    this.contractInfor = row.row
    this.setOpenPopup({
      popupName: 'contractDetail',
      isOpen: true
    })
  }
}
